<template>
    <ItemComponent>
        <div class="project-item" :id="projectId">
            <div class="project-item-header item-header">
                <div class="name subtitle">{{ title }}</div>
                <a class="link" v-if="link" :href="link"><img src="icons/link.png" alt="link.png"></a>
                <a class="media" v-if="media" :href="media"><img src="icons/media.png" alt="media.png"></a>
                <div class="date">{{startDate}} - {{endDate}}</div>
            </div>
            <div class="content">
                <div class="description">{{ description }}</div>
                <div class="tags">
                    <div class="tag" v-for="tag in tags" :key="tag" :style="{ backgroundColor: getColor(tag)}">{{ tag }}</div>
                </div>
            </div>
        </div>
    </ItemComponent>
</template>

<script>
import ItemComponent from "@/components/base/ItemComponent.vue";

export default {
    name: "ProjectItem",
    components: {ItemComponent},
    props: ['projectId', 'title', 'startDate', 'endDate', 'description', 'subtitle', 'tags', 'link', 'media'],
    data() {
        return {
            colorTags: {
                'aws-lambda': '#febd69',
                'aws-api-gateway': '#febd69',
                'aws-cloudfront': '#febd69',
                'aws-s3': '#febd69',
                'aws-ec2': '#febd69',
                'aws-dynamodb': '#febd69',
                'python': '#ffe873',
                'web-development': '#d0d0d0',
                'prompt-engineering': '#d0d0d0',
                'backend-development': '#d0d0d0',
                'aws-engineering': '#d0d0d0',
                'ml-engineering': '#d0d0d0',
            }

        }
    },
    methods: {
        getColor(tag){
            if (tag in this.colorTags){
                return this.colorTags[tag]
            }
            else {
                return '#eee'
            }
        }
    }
}
</script>

<style scoped>
.project-item {
    scroll-margin-top: 50px;
    scroll-behavior: smooth;
}
</style>